.page-voice {
  .voiceList {
    &__card {
      border: none;
      color: $color-text01;
      display: flex;
      justify-content: space-between;
      width: 100%;
      overflow: hidden;
      text-decoration: none;
      margin-bottom: 40px;
      .information {
        flex: 0 0 53.335%;
        position: relative;
        &__customer {
          @include font-line(14, 16);
          font-weight: 700;
          margin: 0 0 8px;
        }
        &__title {
          @include font-line(13, 16);
          font-weight: 700;
          margin: 0;
        }
        &__text {
          @include font-line(12, 15);
          margin: 13px 0;
        }
        &__more {
          width: 101px;
          margin-left: auto;
          .moreButton {
            border: 1px solid #322e2c;
            color: $color-text01;
          }
        }
      }
      .photo {
        flex: 0 0 41.67%;
        height: 29.375vw;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
          font-family: 'object-fit: contain;';
        }
      }
    }
  }

  /*詳細画面*/
  .voice {
    .return {
      width: 101px;
      margin: 0 0 0 auto;
      border: 1px solid #322e2c;
      color: $color-text01;
    }
    &__heading {
      margin: 10px 0;
      @include font-line(15, 22.5);
      font-weight: 700;
    }
    &__top {
      p {
        margin: 15px 0;
      }
    }
    &__customer,
    &__person {
      p {
        @include font-line(13, 19);
        margin: 10px 0;
      }
    }
    &__photo {
      text-align: center;
      width: 100%;
      margin: 0 auto 20px;
      img {
        width: 100%;
      }
      p {
        text-align: left;
      }
      a {
        border: none;
        width: 100%;
      }
    }
    &__photoList {
      margin: 20px 0 0;
      p {
        @include font-line(13, 19);
        margin: 0 0 10px;
      }
    }
  }
}
